import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { fetchProducts } from '../actions/productActions';
//Components
import { Row, Col, Carousel } from 'react-bootstrap';
import Loader from '../components/Loader';
import Message from '../components/Message';

function ProductCarouselScreen({ match }) {
  document.title = 'Henya - Home';
  const dispatch = useDispatch();

  const currency = new Intl.NumberFormat('en-ZA', {
    style: 'currency',
    currency: 'ZAR',
    minimumFractionDigits: 2,
    maximumFractionDigits: 2
  });

  const productsState = useSelector((store) => store.products);
  const { isLoading, error, products } = productsState;

  useEffect(() => {
    dispatch(fetchProducts());
  }, [dispatch]);
  return (
    <>
      <h1 className='my-3'>Our Selection</h1>

      {isLoading ? (
        <Loader loading={isLoading} />
      ) : error ? (
        <Message variant='danger' text={error} />
      ) : (
        <Row className='d-flex justify-content-center'>
          <Col md={10} sm={12}>
            <Carousel interval={1500}>
              {products.map((product) => (
                <Carousel.Item
                  interval={1000}
                  style={{ height: '50vh', width: '100%' }}
                >
                  <img className='w-100 d-block' src={product.image[0]} />
                  <Carousel.Caption
                    style={{
                      background: 'rgba(0,0,0,0.5)',
                      width: '100%',
                      position: 'absolute',
                      zIndex: '0',
                      left: '0'
                    }}
                  >
                    <h3 style={{ color: 'white' }}>{product.name}</h3>
                    <p>{currency.format(product.price)}</p>
                  </Carousel.Caption>
                </Carousel.Item>
              ))}
            </Carousel>
          </Col>
        </Row>
      )}
    </>
  );
}

export default ProductCarouselScreen;
